.footer_outer_container {
  background-color: #111;
  width: 90%;
  margin: 0 auto;
  color: gray;
  padding-top: 40px;
  align-items: center;
  /* justify-content: center;  */
}

.footer_inner_container {
  width: 60%;
}
.footer_data {
  display: flex;
  justify-content: space-between;
}
.footer_data ul {
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 12vhww;
  align-items: center;
  padding-top: 20px;
}
.footer_data ul li {
  cursor: pointer;
}
